// CSS Entry File
//SCSS lets you visually nest selectors more easily
//will get converted into main css
// @import './styles/reset.css';

*{
	margin: 0;
	padding: 0;
  // font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: 'pokemongba';
  font-size: 15px;
}
@font-face {
  font-family: 'pokemongba';
  font-style: 'regular';
  font-weight: 300;
  src: url('../assets/PokemonGb-RAeo.ttf');
}
#body{
  background-color: slategray;
  display:flex;
  flex-direction: column;
  // align-items: center;
  margin:20px;
}
#title{
  text-align: center;
}
.content-container{
  display: flex;
  flex-direction: row;
  // justify-content: center;
  width: 100%;
  height: 100vh;
  margin-top:10px;
}
#left-container{
  display: flex;
  flex-direction: column;
  margin-right:10px;
  height: 90%;
  // border-radius: 50px;

}
#canvas{
  // width:60vw;
  // height: 70%;
  border-radius: 10px;
  margin-bottom:5px;
  // border:rgb(0, 0, 0) 3px solid;
}
#dialoguebox{
	background-color: lightblue;
	height: 30%;
  width:900px;
 
	// display: flex;
  border-radius: 10px;
}

#dialogueButtons{
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;

  
}
#dialoguebox{
  display:flex;
  flex-direction: row;
  padding:20px;
  align-items: center;
  justify-content: space-around;
}
#dialoguebox img{
  margin-right:30px;
}
#dialogue-text-container{
  width: 60%;
  display: flex;
  justify-content: center;
}
#dialoguetext1{
	display: flex;
	align-items: center;
	justify-content: center;
}

button {
  // transition: transform 150ms, box-shadow 150ms;
  padding: 10px 30px;
  border-radius: 2px;
  cursor:pointer;
  }

button:hover { 
  box-shadow: rgba(0, 0, 0, .15) 0 3px 9px 0;
  transform: translateY(-2px);
}

@media (min-width: 768px) {
  .default-content-right {
    padding: 0px 20px;
  }
}
#pokedex_pic_bottom{
  height:100px;
  width: auto;
  border-radius: 5px;
}


#rightside-container{
  background-color: rgb(156, 235, 156);
  width: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  border-radius: 10px;
  position:relative;
}
#rightside-container button{
  font-size: 20px;
  padding: 15px 30px
}
#rightside-container > *{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  border-radius: 2px;
}

.pokedex-item{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  padding:10px;
}
.pokedex-item:after{
  content:'';
  width:100%;
  height:2px;
  margin-top:15px;
  background-color:black;
  border-radius:5px;
}

.pokedex-item-initial-content{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px;
  padding-left:20px;
  padding-right:20px;
}
.pokedex-item-initial-content img{
  border-radius: 5px;
}
.pokedex-item-image-name{
  display:flex;
  flex-direction: column;
  align-items: center;
  width:30%;
  padding-right:30px;
}
.pokedex-item-image-name h2{
  padding-top:5px;
}
.pokedex-friendship-container{
  width:70%;
}
.pokedex-friendshiplevel{
  font-size: 14px;
  // font-weight: bold;
  line-height: 1;
  margin-top:5px;
}
#bagon-progressdiv{
  padding:5px;
  margin-left:10px;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  row-gap: 5px;
}


#dialogue-text-container{
  display: flex;
  // justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.divider {
  flex-grow: 1;
  border-bottom: 1px solid black;
  margin: 5px
}

#instructions-container{
  padding: 25px;
}
#instructions-container h2{
  font-size:20px;
  padding-bottom: 10px;
}
#instructions-container p{
  padding-bottom: 10px;
  text-align: center;
  font-weight: bold;
  line-height: 1.35;
}

.backButton{
  background-color: #d9d9d9;
  top:0;
}

.hidden{
  display: none !important;
}
#teamrocket-sprite-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding: 10px;
  padding-left:20px;
  padding-right:20px;
  // position:absolute;
  // top:100px;
  // left:400px;
}
#misc-bar-right{
  align-self: flex-start;
  position:absolute;
  top:0px;
  left:10px;
  display:flex;
  flex-direction: row;
}
#mute-button{
  background: none;
  border: none;
  padding:10px !important;
  scale: 2;
  // position:relative;
}
#mute-button:hover{
  box-shadow: none;
  transform: scale(1.3);
  transition: transform 0.2s;
}
#teamrocket-sprite-container button{
  border:none;
  background-color: transparent;
  padding:30px
}
#jessie, #james{
  width: 16px; /* set the width and height of the sprite */
  height: 20px;
  scale: 2;
  // background-image: url('../assets/Jessie_16x20.png'); /* set the URL of the sprite sheet */
  // background-position: 0 0; /* set the position of the top-left sprite */
  // background-size: 40px 100px; 
  object-fit: none;
  object-position: left top;
}

#links-to-patrick{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
#links-to-patrick img{
  width: 50px;
  height: 50px;
  margin: 10px;
}
#links-to-patrick img:hover{
  transform: scale(1.1);
  transition: transform 0.2s;
}